import React,{useState} from 'react';
import './MovieCard.scss'
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import axios from 'axios'
const MovieCard = ({item, newitem}) => {
    const navigate = useNavigate();
    const {title,link,image,pubDate,userRating} = item
    let replaceTitle = title.replace(/(<([^>]+)>)/gi, "")
    

    const moveToDetail = () => {
      navigate('/MovieDetail')
      // console.log(item);
      newitem(item)
    }
  return (
    <>
    <div className="movie-card-container" onClick={()=>{moveToDetail()}}>
      <CardImg image={image}/>
      <div className="movie-text">
        <h2>{replaceTitle}</h2>
        <div>{`${pubDate}년도`}</div>
        <div className="movie-summary-row">
          <h5>평점</h5>
        </div>
        <div className="movie-likes">{`${userRating}/ 10`}</div>
        <div className='btn-container'><button className='movie-detail-btn'>자세히보기</button></div>
      </div>
    </div>
   
    </>
  );
};

// 음..지금 card에서 제목 클릭하면 그 영화 정보 가져오기는 했음
// 클릭시 디테일 페이지로 넘어가서 보여주기를 해야되는데 data를 어떻게 넘겨야 될까..?
// card는 moviesearchcontainer에 있고 item이라는 props 로 받고있음
// 근데 지금 봤을때 detail state를 보내줄수가 없기때문에 다른 상태관리를 써보자
// 아니면 click한 제목을 서버로 넘겨주고 서버는 그 제목을 detail에서 get요청 했을때 
// 보내주면 그걸로 페이지 구성하기 
const CardImg = styled.div`
  width: 100%;
  height: 300px;
  background-image: url(${(props)=> props.image});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 111 !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(0.35, rgba(0, 0, 0, 1)),
    color-stop(0.5, rgba(0, 0, 0, 1)),
    color-stop(0.65, rgba(0, 0, 0, 1)),
    color-stop(0.85, rgba(0, 0, 0, 0.6)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  position: relative;
`;
export default MovieCard;